import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Layout from "./Layout";
import { toast } from "react-toastify";
import axios from "axios";
import { useLoading } from "./LoadingContext";
import { useCart } from "./CartContext";

const styles = {
  container: {
    padding: "80px",
    width: "90%",
  },
  title: {
    fontSize: "28px",
    marginBottom: "20px",
    color: "#333",
    fontWeight: "bold",
  },
  image: {
    width: "350px",
    height: "300px",
    borderRadius: "8px",
    boxShadow: "0px 1px 1px rgba(0, 0, 0, 0.2)",
  },
  details: {
    backgroundColor: "#fff",
    padding: "20px",
    borderRadius: "8px",
    boxShadow: "0px 1px 1px rgba(0, 0, 0, 0.2)",
  },

  btn: {
    backgroundColor: "#fff",
    // margin: "20px",
    borderRadius: "8px",
    boxShadow: "0px 1px 1px rgba(0, 0, 0, 0.2)",
  },
  label: {
    fontSize: "18px",
    fontWeight: "bold",
    color: "#333",
  },
  info: {
    fontSize: "16px",
    color: "#555",
    marginBottom: "10px",
  },
  actualPrice: {
    fontSize: "18px",
    color: "#555",

    marginBottom: "10px",
  },
};

const Details = () => {
  const { id } = useParams();
  const [product, setProduct] = useState([]);
  const [similarProducts, setSimilarProducts] = useState([]);
  const { isLoading, showLoading, hideLoading, toggleLoading } = useLoading();
  const { addItemToCart } = useCart();
  const navigate = useNavigate();
  useEffect(() => {
    getSingleProduct();
    getSimilarProducts();
  }, [id]);
  const handleAddToCart = (product) => {
    addItemToCart(product);
  };

  const getSingleProduct = async () => {
    try {
      const response = await axios.get(
        `http://localhost:3010/product/singleProduct/${id}`
      );
      if (response.data.success) {
        setProduct(response.data.product);
        console.log(response);
      }
    } catch (e) {
      toast.error(e.message);
    }
  };
  const getSimilarProducts = async () => {
    try {
      const response = await axios.get(
        `http://localhost:3010/product/similarProducts/${product._id}/${product.category}`
      );
      if (response.data.success) {
        console.log(response);
        setSimilarProducts(response.data.products);
      }
    } catch (e) {
      toast.error(e.message);
    }
  };

  const nav = (p, id) => {
    showLoading();
    setTimeout(() => {
      navigate(`/${p}/${id}`);
      hideLoading();
    }, 1300);
  };
  return (
    <Layout>
      <div style={styles.container}>
        {product ? (
          <>
            <div>
              {/* <h2 style={styles.title}>Product Details</h2> */}
              <div className="row">
                <div className="col-md-4">
                  <img
                    src={`http://localhost:3010/${product?.image}`}
                    alt={product.name}
                    style={styles.image}
                  />
                </div>
                <div className="col-md-8" style={styles.details}>
                  {/* <p style={styles.label}>ID:</p> */}
                  {/* <p style={styles.info}>{product._id}</p> */}
                  <div className="row">
                    <div className="col-6">
                      <p style={styles.label}>Name:</p>
                      <p style={styles.info}>{product.name}</p>
                      <p style={styles.label}>Description:</p>
                      <p style={styles.info}>{product.description}</p>
                      <p style={styles.label}>Actual Price:</p>
                      <p style={styles.actulaPrice}>{product.actualPrice}</p>
                    </div>
                    <div className="col-6">
                      <p style={styles.label}>Dicounted Price:</p>
                      <p style={styles.actulaPrice}>{product.price}</p>

                      <p style={styles.label}>Status:</p>
                      <p style={styles.actulaPrice}>
                        {product.quantity > 0 ? (
                          <span className="text-success"> In Stock</span>
                        ) : (
                          <span className="text-danger">Out of Stock</span>
                        )}
                      </p>
                      {product.quantity > 0 ? (
                        <button
                          className="btn btn-primary"
                          onClick={() => handleAddToCart(product)}
                        >
                          Add to Cart
                        </button>
                      ) : (
                        ""
                      )}
                    </div>
                  </div>
                  {/* Add more details here */}
                </div>
              </div>
            </div>
            <hr />
            <h2 className="my-5 p-3">Similar Products</h2>
            <hr />
            <div className="row mt-4">
              {similarProducts.map((product) => (
                <div key={product._id} className="col-md-4 mb-3">
                  <div
                    className="card"
                    style={{ cursor: "pointer" }}
                    onClick={() => nav("Details", product._id)}
                  >
                    <img
                      src={`http://localhost:3010/${product?.image}`}
                      className="card-img-top"
                      alt={product.name}
                      style={{ height: "270px" }}
                    />
                    <div className="card-body">
                      <h5 className="card-title">{product.name}</h5>
                      <hr />
                      <p className="card-text">Rs.{product.price}</p>

                      {product.actualPrice ? (
                        <p
                          className="card-text"
                          style={{ textDecoration: "line-through" }}
                        >
                          Rs.{product.actualPrice}
                        </p>
                      ) : (
                        <p className="card-text"> `</p>
                      )}
                    </div>
                    <div className="card-footer"></div>
                  </div>
                </div>
              ))}
            </div>
          </>
        ) : (
          <p>Loading...</p>
        )}
      </div>
    </Layout>
  );
};

export default Details;
