import axios from "axios";
import { createContext, useContext, useEffect, useState } from "react";

// Create a new context
const AuthContext = createContext();

// Create a custom hook to access the context
export function useAuth() {
  return useContext(AuthContext);
}

// Create an auth provider component
export function AuthProvider({ children }) {
  const [user, setUser] = useState(null);

  // Load user details from localStorage on component mount
  useEffect(() => {
    const storedUser = localStorage.getItem("user");
    if (storedUser) {
      const parsedUser = JSON.parse(storedUser);
      setUser(parsedUser);
      if (parsedUser.token) {
        axios.defaults.headers.common["Authorization"] = parsedUser.token;
      }
    }
  }, []);
  // Update user details and save to localStorage
  const updateUser = (newUser) => {
    setUser(newUser);
    localStorage.setItem("user", JSON.stringify(newUser));
  };
  if (user?.token) {
    axios.defaults.headers.common["Authorization"] = user?.token;
    console.log("Sendling token");
  }
  // Clear user details and remove from localStorage
  const clearUser = () => {
    setUser(null);
    localStorage.removeItem("user");
    // Clear Axios authorization header
    delete axios.defaults.headers.common["Authorization"];
  };

  const contextValue = {
    user,
    updateUser,
    clearUser,
  };

  return (
    <AuthContext.Provider value={contextValue}>{children}</AuthContext.Provider>
  );
}

export default AuthContext;
