import React from "react";

const footerStyles = {
  backgroundColor: "#f0f0f0",
  padding: "20px",
  width: "100%",
  textAlign: "center",
  borderTop: "1px solid #ddd",
};

const Footer = () => {
  return (
    <footer style={footerStyles}>
      <p>&copy; 2023 Your E-commerce Store</p>
    </footer>
  );
};

export default Footer;
