import React from "react";
import Layout from "./Layout";
import { useCart } from "./CartContext";

const Cart = () => {
  const {
    cart,
    removeItemFromCart,
    clearCart,
    increaseItemQuantity,
    decreaseItemQuantity,
    calculateTotalPrice,
  } = useCart();

  const emptyCartStyles = {
    textAlign: "center",
    marginTop: "20px",
    marginBottom: "20px",
    fontSize: "20px",
  };

  const containerStyles = {
    maxWidth: "800px",
    margin: "0 auto",
  };

  const tableStyles = {
    width: "100%",
    borderCollapse: "collapse",
  };

  const thStyles = {
    background: "#f2f2f2",
    padding: "10px",
  };

  const tdStyles = {
    padding: "10px",
    borderBottom: "1px solid #ccc",
  };

  const quantityButtonStyles = {
    backgroundColor: "#007bff",
    color: "white",
    borderRadius: "50%",
    width: "25px",
    height: "25px",
    border: "none",
    cursor: "pointer",
  };

  const totalPriceStyles = {
    textAlign: "end",
    fontSize: "18px",
    marginTop: "10px",
    fontWeight: "bold",
  };

  const removeButtonStyles = {
    backgroundColor: "#dc3545",
    color: "white",
    border: "none",
    cursor: "pointer",
    fontSize: "14px",
    padding: "10px 20px",
    borderRadius: "5px",
    cursor: "pointer",
    // textDecoration: "underline",
  };

  const clearCartButtonStyles = {
    backgroundColor: "#dc3545",
    color: "white",
    padding: "10px 20px",
    borderRadius: "5px",
    border: "none",
    cursor: "pointer",
  };
  const quantityDisplayStyles = {
    display: "flex",
    alignItems: "center",
  };

  const quantityTextStyles = {
    margin: "0 5px",
    fontSize: "16px",
  };
  if (cart.length <= 0) {
    return (
      <Layout>
        <div style={emptyCartStyles}>
          <h2>Your Cart is Empty</h2>
        </div>
      </Layout>
    );
  }

  return (
    <Layout>
      <div style={containerStyles}>
        <h2>Shopping Cart ({cart.length})</h2>
        <table style={tableStyles}>
          <thead>
            <tr>
              <th style={thStyles}>Product</th>
              <th style={thStyles}>Price</th>
              <th style={thStyles}>Quantity</th>
              <th style={thStyles}>Actions</th>
            </tr>
          </thead>
          <tbody>
            {cart.map((item) => (
              <tr key={item.id}>
                <td style={tdStyles}>{item.name}</td>
                <td style={tdStyles}>Rs.{item.price}</td>
                <td style={tdStyles}>
                  <div style={quantityDisplayStyles}>
                    <button
                      style={quantityButtonStyles}
                      onClick={() => decreaseItemQuantity(item._id)}
                    >
                      -
                    </button>
                    <span style={quantityTextStyles}>{item.quantity}</span>
                    <button
                      style={quantityButtonStyles}
                      onClick={() => increaseItemQuantity(item._id)}
                    >
                      +
                    </button>
                  </div>
                </td>
                <td style={tdStyles}>
                  <button
                    style={removeButtonStyles}
                    onClick={() => removeItemFromCart(item._id)}
                  >
                    Remove
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        <p style={totalPriceStyles}>Total Price: Rs.{calculateTotalPrice()}</p>
        <div style={{ textAlign: "end" }}>
          <button style={clearCartButtonStyles} onClick={clearCart}>
            Clear Cart
          </button>
        </div>
      </div>
    </Layout>
  );
};

export default Cart;
