import React from "react";
import { useLoading } from "./LoadingContext";
import CircularProgress from "@mui/material/CircularProgress";
import Layout from "./Layout";

const Loading = () => {
  const { isLoading } = useLoading();
  if (!isLoading) {
    return null; // If isLoading is false, don't render anything
  }
  const loaderStyle = {
    container: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      height: "100vh", // This will make the container take the full viewport height
    },
    circularProgress: {
      color: "#ff6600", // Change the color here
    },
  };
  return (
    <Layout>
      <div style={loaderStyle.container}>
        <CircularProgress style={loaderStyle.circularProgress} />
        {/* Loading */}
      </div>
    </Layout>
  );
};

export default Loading;
