import React, { useState, useEffect } from "react";
import Layout from "./Layout";
import { useParams } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import { useLoading } from "./LoadingContext";
import axios from "axios";
import { toast } from "react-toastify";

const Category = () => {
  const { id } = useParams();
  const { isLoading, showLoading, hideLoading, toggleLoading } = useLoading();
  const [category, setCategory] = useState([]);
  const [categoriesData, setCategoriesData] = useState([]);
  const navigate = useNavigate();

  const [priceRange, setPriceRange] = useState([0, 10000]); // Set the initial price range

  const handlePriceRangeChange = (e) => {
    setPriceRange([parseFloat(e.target.value), priceRange[1]]);
  };

  const handlePriceRangeChangeEnd = (e) => {
    setPriceRange([priceRange[0], parseFloat(e.target.value)]);
  };

  const filterProductsByPrice = () => {
    return category.filter((product) => {
      const productPrice = parseFloat(product.price);
      return productPrice >= priceRange[0] && productPrice <= priceRange[1];
    });
  };

  const filteredProducts = filterProductsByPrice();

  const handleFilterSubmit = (e) => {
    e.preventDefault();
    const filteredProducts = filterProductsByPrice();
    setCategory(filteredProducts);
  };

  const nav = (p, id) => {
    // Define the 'nav' function here
    showLoading();
    setTimeout(() => {
      navigate(`${p}/${id}`);
      hideLoading();
    }, 1300);
  };

  useEffect(() => {
    fetchCategoryById();
    fetchCategories();
  }, []);

  const fetchCategoryById = async () => {
    try {
      const response = await axios.get(
        `http://localhost:3010/product/Category/${id}`
      );

      if (response.data.success) {
        setCategory(response.data.products);
      } else {
        toast.error(response.data.error);
      }
    } catch (error) {
      toast.error("An error occurred: " + error.message);
    }
  };

  const fetchCategories = async () => {
    try {
      const response = await axios.get("http://localhost:3010/category/get");

      if (response.data.success) {
        setCategoriesData(response.data.categories);
      } else {
        toast.error(response.data.error);
      }
    } catch (error) {
      toast.error("An error occurred: " + error.message);
    }
  };

  const ulStyles = {
    listStyleType: "disc",
    marginRight: "60px",
    marginTop: "20px",
    backgroundColor: "#f9f9f9",
    paddingLeft: "30px",
    listStyleType: "none",
    padding: "30px",
    border: "1px solid #ddd",
    borderRadius: "15px",
  };

  const liStyles = {
    marginBottom: "10px",
    padding: "5px",
    border: "1px solid #ddd",
    borderRadius: "5px",
    color: "#333",
    fontSize: "1rem",
    backgroundColor: "white",
    cursor: "pointer",
  };

  return (
    <Layout>
      <div className="container pt-5">
        <div className="row">
          <div className="col-3 p-1 w-25">
            <h4>Filter By Category</h4>
            <ul style={ulStyles}>
              {Array.isArray(categoriesData) ? (
                categoriesData.map((category, index) => (
                  <li
                    style={liStyles}
                    key={index}
                    onClick={() => nav("/Category", category._id)}
                  >
                    {category.name}
                  </li>
                ))
              ) : (
                <p>No categories available</p>
              )}
            </ul>
            <h4>Filter By Price</h4>
            <form onSubmit={handleFilterSubmit}>
              <div className="form-group">
                <label htmlFor="priceRange">Price Range:</label>
                <input
                  type="range"
                  id="priceRange"
                  className="form-control-range"
                  min="0"
                  max="10000"
                  step="1"
                  value={priceRange[0]}
                  onChange={handlePriceRangeChange}
                  onMouseUp={handlePriceRangeChangeEnd}
                />
                <input
                  type="range"
                  id="priceRange"
                  className="form-control-range"
                  min="0"
                  max="10000"
                  step="1"
                  value={priceRange[1]}
                  onChange={handlePriceRangeChangeEnd}
                />
                <span>{priceRange[0]}</span> - <span>{priceRange[1]}</span>
              </div>
            </form>
          </div>
          {Array.isArray(filteredProducts) ? (
            filteredProducts.map((product) => (
              <div key={product._id} className="col-md-3 mb-3">
                <div
                  className="card"
                  style={{ cursor: "pointer" }}
                  onClick={() => nav("/Details", product._id)}
                >
                  <img
                    src={`http://localhost:3010/${product?.image}`}
                    className="card-img-top"
                    alt={product.name}
                    style={{ height: "270px" }}
                  />
                  <div className="card-body">
                    <h5 className="card-title">{product.name}</h5>
                    <hr />
                    <p className="card-text">Rs.{product.price}</p>
                  </div>
                  <div className="card-footer"></div>
                </div>
              </div>
            ))
          ) : (
            <p>No products available for the selected price range.</p>
          )}
        </div>
      </div>
    </Layout>
  );
};

export default Category;
