import React from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { useLoading } from "../LoadingContext";
import { useAuth } from "../AuthContext";
import { toast } from "react-toastify";

const styles = {
  adminNav: {
    backgroundColor: "#007BFF",
    color: "#fff",
    padding: "10px",
    boxShadow: "0px 2px 5px rgba(0, 0, 0, 0.2)",
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    height: "60px",
  },
  logo: {
    fontSize: "24px",
    fontWeight: "bold",
    textDecoration: "none",
    color: "#fff",
  },
  ul: {
    listStyle: "none",
    padding: 0,
    margin: 0,
    display: "flex",
  },
  li: {
    marginRight: "20px",
  },
  navLink: {
    textDecoration: "none",
    color: "#fff",
    fontWeight: "bold",
  },
};

const AdminNav = () => {
  const { isLoading, showLoading, hideLoading, toggleLoading } = useLoading();
  const { updateUser, user, clearUser } = useAuth();
  const navigate = useNavigate();
  const nav = (p) => {
    showLoading();
    setTimeout(() => {
      navigate(p);
      hideLoading();
    }, 900);
  };
  return (
    <nav style={styles.adminNav}>
      <NavLink to="DashBoard" style={styles.logo}>
        Admin Dashboard
      </NavLink>
      <ul style={styles.ul}>
        <li style={styles.li}>
          <NavLink
            to="/admin/DashBoard"
            style={styles.navLink}
            onClick={() => nav("/admin/DashBoard")}
          >
            Dashboard
          </NavLink>
        </li>
        <li style={styles.li}>
          <NavLink
            to="/admin/users"
            style={styles.navLink}
            onClick={() => nav("/admin/users")}
          >
            Users
          </NavLink>
        </li>
        <li style={styles.li}>
          <NavLink
            to="/admin/products"
            style={styles.navLink}
            onClick={() => nav("/admin/products")}
          >
            Products
          </NavLink>
        </li>
        <li style={styles.li}>
          <NavLink
            to="/admin/Categories"
            style={styles.navLink}
            onClick={() => nav("/admin/Categories")}
          >
            Categories
          </NavLink>
        </li>

        <li style={styles.li}>
          <NavLink
            to="/admin/Slider"
            style={styles.navLink}
            onClick={() => nav("/admin/Slider")}
          >
            Slider
          </NavLink>
        </li>
      </ul>
    </nav>
  );
};

export default AdminNav;
