import Carousel from "react-bootstrap/Carousel";
import React, { useState, useEffect } from "react";
import axios from "axios";
import Layout from "./Layout";
import { useCart } from "./CartContext";
import { useNavigate } from "react-router-dom";
import { useLoading } from "./LoadingContext";
import { toast } from "react-toastify";

const Home = () => {
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState({});

  const [SliderName, setSliderName] = useState("");
  const [Sliders, setSliders] = useState([]);
  const [image, setSliderImage] = useState(null);
  const [categoriesData, setCategoriesData] = useState({});
  const { addItemToCart } = useCart();
  const { isLoading, showLoading, hideLoading, toggleLoading } = useLoading();
  const navigate = useNavigate();
  useEffect(() => {
    // Fetch Sliders when the component mounts
    fetchSliders();
  }, []);
  const fetchSliders = async () => {
    try {
      const response = await axios.get("http://localhost:3010/slider/get");
      // console.log(response);
      if (response.data.success) {
        // Set the fetched Sliders in the state
        setSliders(response.data.Sliders);
      } else {
        toast.error(response.data.error);
      }
    } catch (error) {
      toast.error("An error occurred: " + error.message);
    }
  };

  const nav = (p, id) => {
    showLoading();
    console.log("erroroor");
    setTimeout(() => {
      navigate(`${p}/${id}`);
      hideLoading();
    }, 1300);
  };

  useEffect(() => {
    // Fetch products data when the component mounts
    axios
      .get("http://localhost:3010/product/get")
      .then((response) => {
        // Set the products state with the data from the API
        setProducts(response.data.products);
      })
      .catch((error) => {
        console.error("Error fetching products:", error);
      });

    // Fetch category data when the component mounts
    axios
      .get("http://localhost:3010/category/get")
      .then((response) => {
        // Create a map of category IDs to category names
        const categoryMap = {};
        response.data.categories.forEach((category) => {
          categoryMap[category._id] = category.name;
        });
        setCategoriesData(categoryMap);
      })
      .catch((error) => {
        console.error("Error fetching categories:", error);
      });
  }, []);

  const ulStyles = {
    listStyleType: "disc", // Bullet list style
    marginLeft: "40px",
    backgroundColor: "#f9f9f9", // Background color for list items
    paddingLeft: "30px",
    // Remove default padding
    listStyleType: "none",
    padding: "30px",
    border: "1px solid #ddd", // Add a border
    borderRadius: "15px",
  };

  const liStyles = {
    marginBottom: "10px",
    padding: "5px",
    border: "1px solid #ddd", // Add a border
    borderRadius: "5px",
    color: "#333", // Text color
    fontSize: "1rem",
    backgroundColor: "white",
    cursor: "pointer",
  };

  const liHoverStyles = {
    backgroundColor: "#eee", // Hover effect
    cursor: "pointer",
  };

  useEffect(() => {
    // Fetch categories when the component mounts
    fetchCategories();
  }, []);

  const fetchCategories = async () => {
    try {
      const response = await axios.get("http://localhost:3010/category/get");

      if (response.data.success) {
        // Set the fetched categories in the state
        setCategoriesData(response.data.categories);
      } else {
        toast.error(response.data.error);
      }
    } catch (error) {
      toast.error("An error occurred: " + error.message);
    }
  };

  return (
    <Layout>
      <div className="row p-5">
        <div className="col-3">
          <ul style={ulStyles}>
            {Array.isArray(categoriesData) ? (
              categoriesData.map((category, index) => (
                <li
                  style={liStyles}
                  key={index}
                  onClick={() => nav("Category", category._id)}
                >
                  {category.name}
                </li>
              ))
            ) : (
              <p>No categories available</p>
            )}
          </ul>
        </div>
        <div className="col-9">
          <Carousel data-bs-theme="dark">
            {Sliders.map((Slider, index) => (
              <Carousel.Item>
                <img
                  className="d-block w-100"
                  src={`http://localhost:3010/${Slider?.image}`}
                  alt="First slide"
                  height="400"
                />
                <Carousel.Caption>
                  {/* <h5>{Slider.name}</h5> */}
                  {/* <p>
                    Nulla vitae elit libero, a pharetra augue mollis interdum.
                  </p> */}
                </Carousel.Caption>
              </Carousel.Item>
            ))}
          </Carousel>
        </div>
      </div>

      <div className="p-4">
        <h2>Just For You</h2>
        <div className="row p-5">
          {products.map((product) => (
            <div key={product._id} className="col-md-3 mb-3">
              <div
                className="card"
                style={{ cursor: "pointer" }}
                onClick={() => nav("Details", product._id)}
              >
                <img
                  src={`http://localhost:3010/${product?.image}`}
                  className="card-img-top"
                  alt={product.name}
                  style={{ height: "270px" }}
                />
                {console.log(`http://localhost:3010/${product?.image}`)}
                <div className="card-body">
                  <h5 className="card-title">{product.name}</h5>
                  <hr />
                  <p className="card-text">Rs.{product.price}</p>

                  {product.actualPrice ? (
                    <p
                      className="card-text"
                      style={{ textDecoration: "line-through" }}
                    >
                      Rs.{product.actualPrice}
                    </p>
                  ) : (
                    <p className="card-text"> `</p>
                  )}
                </div>
                <div className="card-footer"></div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </Layout>
  );
};

export default Home;
