import React, { useState, useEffect } from "react";
import Layout from "../Layout";
import axios from "axios";
import { toast } from "react-toastify";
import { useAuth } from "../AuthContext";
import { useNavigate } from "react-router-dom";
import { useLoading } from "../LoadingContext";

const Pro = () => {
  const [productName, setProductName] = useState("");
  const [productDescription, setProductDescription] = useState("");
  const [productPrice, setProductPrice] = useState("");
  const [actualPrice, setActualtPrice] = useState("");
  const [productImage, setProductImage] = useState(null);
  const [productQuantity, setProductQuantity] = useState("");
  const [productCategory, setProductCategory] = useState("");
  const [categories, setCategories] = useState([]);
  const { updateUser, user, clearUser } = useAuth();
  const { showLoading, hideLoading } = useLoading();
  const navigate = useNavigate();
  const nav = (p) => {
    showLoading();
    setTimeout(() => {
      navigate(p);
      hideLoading();
    }, 900);
  };
  // State to store fetched categories
  useEffect(() => {
    // Fetch categories when the component mounts
    fetchCategories();
  }, []);

  const fetchCategories = async () => {
    try {
      const response = await axios.get("http://localhost:3010/category/get");

      if (response.data.success) {
        // Set the fetched categories in the state
        setCategories(response.data.categories);
      } else {
        toast.error(response.data.error);
      }
    } catch (error) {
      toast.error("An error occurred: " + error.message);
    }
  };
  const handleImageChange = (e) => {
    const selectedImage = e.target.files[0];
    setProductImage(selectedImage);
    console.log(productImage, 12);
  };
  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const formData = new FormData();
      formData.append("name", productName);
      formData.append("description", productDescription);
      formData.append("price", productPrice);
      formData.append("actualPrice", actualPrice);
      formData.append("image", productImage);
      formData.append("quantity", productQuantity);
      formData.append("category", productCategory);

      const response = await axios.post(
        "http://localhost:3010/product/add",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      );

      if (response.data.success) {
        toast.success(response.data.message);
        setProductName("");
        setProductDescription("");
        setProductPrice("");
        setProductImage("");
        setProductQuantity("");
        setProductCategory("");
      } else {
        toast.error(response.data.error);
      }
    } catch (error) {
      toast.error("An error occurred: " + error.message);
    }
  };
  if (user?.role === 0) {
    nav("/home");
    console.log("Ok");
    toast.error("Invalid Access");
  }
  return (
    <Layout>
      <div style={styles.container}>
        <h2 style={styles.heading}>Add Product</h2>
        <form onSubmit={handleSubmit} encType="multipart/form-data">
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="productName">
              Product Name:
            </label>
            <input
              style={styles.input}
              type="text"
              id="productName"
              value={productName}
              onChange={(e) => setProductName(e.target.value)}
              required
            />
          </div>
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="productDescription">
              Product Description:
            </label>
            <textarea
              style={styles.input}
              id="productDescription"
              value={productDescription}
              onChange={(e) => setProductDescription(e.target.value)}
              required
            />
          </div>
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="productPrice">
              Actual Product Price:
            </label>
            <input
              style={styles.input}
              type="number"
              id="productPrice"
              value={actualPrice}
              onChange={(e) => setActualtPrice(e.target.value)}
              required
            />
          </div>
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="productPrice">
              Product Price:
            </label>
            <input
              style={styles.input}
              type="number"
              id="productPrice"
              value={productPrice}
              onChange={(e) => setProductPrice(e.target.value)}
              required
            />
          </div>
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="productImage">
              Product Image:
            </label>
            <input
              style={styles.input}
              type="file"
              id="productImage"
              accept="image/*"
              onChange={handleImageChange}
              required
              // value={productImage}
            />
          </div>
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="productQuantity">
              Product Quantity:
            </label>
            <input
              style={styles.input}
              type="number"
              id="productQuantity"
              value={productQuantity}
              onChange={(e) => setProductQuantity(e.target.value)}
              required
            />
          </div>
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="productCategory">
              Product Category:
            </label>
            <select
              style={styles.input}
              id="productCategory"
              value={productCategory}
              onChange={(e) => setProductCategory(e.target.value)}
              required
            >
              <option value="">Select a category...</option>
              {categories.map((category) => (
                <option key={category.id} value={category._id}>
                  {category.name}
                </option>
              ))}
            </select>
          </div>
          <button style={styles.button} type="submit">
            Add Product
          </button>
        </form>
      </div>
    </Layout>
  );
};

const styles = {
  container: {
    maxWidth: "400px",
    margin: "50px auto 0",
    padding: "20px 40px",
    border: "1px solid #ccc",
    borderRadius: "5px",
    backgroundColor: "#fff",
    boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
  },
  heading: {
    fontSize: "24px",
    textAlign: "center",
    marginBottom: "20px",
  },
  formGroup: {
    marginBottom: "20px",
  },
  label: {
    display: "block",
    fontWeight: "bold",
    marginBottom: "5px",
  },
  input: {
    width: "100%",
    padding: "10px",
    border: "1px solid #ccc",
    borderRadius: "3px",
    fontSize: "16px",
  },
  button: {
    display: "block",
    width: "100%",
    padding: "10px",
    backgroundColor: "#ff6600",
    color: "#fff",
    border: "none",
    borderRadius: "3px",
    cursor: "pointer",
    fontSize: "18px",
    marginTop: "10px",
  },
};

export default Pro;
