import React, { useState, useEffect } from "react";
import Layout from "../Layout";
import axios from "axios";
import { toast } from "react-toastify";

const Categories = () => {
  // State variables for input fields
  const [categoryName, setCategoryName] = useState("");
  const [categories, setCategories] = useState([]);
  const [showForm, setShowForm] = useState(false);
  const [image, setcategoryImage] = useState(null);
  useEffect(() => {
    // Fetch categories when the component mounts
    fetchCategories();
  }, []);

  const fetchCategories = async () => {
    try {
      const response = await axios.get("http://localhost:3010/category/get");

      if (response.data.success) {
        // Set the fetched categories in the state
        setCategories(response.data.categories);
      } else {
        toast.error(response.data.error);
      }
    } catch (error) {
      toast.error("An error occurred: " + error.message);
    }
  };

  const handleImageChange = (e) => {
    const selectedImage = e.target.files[0];
    setcategoryImage(selectedImage);
    console.log(image);
  };
  // Function to handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const formData = new FormData();
      formData.append("name", categoryName);
      formData.append("image", image);
      const response = await axios.post(
        "http://localhost:3010/category/add",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      );

      // Check the response status and handle accordingly
      if (response.data.success) {
        // Category added successfully, you can perform any necessary actions here
        toast.success(response.data.message);
        setCategoryName("");
        fetchCategories(); // Fetch updated categories after adding a new one
      } else {
        // Handle errors or display an error message
        toast.error(response.data.error);
      }
    } catch (error) {
      // Handle any network or other errors here
      toast.error("An error occurred:", error);
      console.log(error);
    }
  };
  return (
    <Layout>
      {showForm ? (
        <button
          style={styles.addButton}
          styles={{ width: "100px" }}
          onClick={() => setShowForm(!showForm)}
        >
          Show data
        </button>
      ) : (
        <button
          style={styles.addButton}
          styles={{ width: "100px" }}
          onClick={() => setShowForm(!showForm)}
        >
          Add New
        </button>
      )}

      <div style={styles.container}>
        {showForm ? (
          <>
            <h2 style={styles.heading}>Add Category</h2>
            <form onSubmit={handleSubmit}>
              <div style={styles.formGroup}>
                <label style={styles.label} htmlFor="categoryName">
                  Category Name:
                </label>
                <input
                  style={styles.input}
                  type="text"
                  id="categoryName"
                  value={categoryName}
                  onChange={(e) => setCategoryName(e.target.value)}
                  required
                />
              </div>
              <div style={styles.formGroup}>
                <label style={styles.label} htmlFor="categoryImage">
                  Product Image:
                </label>
                <input
                  style={styles.input}
                  type="file"
                  id="categoryImage"
                  accept="image/*"
                  onChange={handleImageChange}
                  required
                  // value={categoryImage}
                />
              </div>
              <button style={styles.button} type="submit">
                Add Category
              </button>
            </form>
          </>
        ) : (
          <div>
            <h2 style={styles.heading}>Categories</h2>
            <table style={styles.table}>
              <thead>
                <tr>
                  <th style={styles.th}>Sr.</th>
                  <th style={styles.th}>Image</th>
                  <th style={styles.th}>Name</th>
                  <th style={styles.th}>Delete</th>
                </tr>
              </thead>
              <tbody>
                {categories.map((category, index) => (
                  <tr key={category.id}>
                    <td style={styles.td}>{index + 1}</td>
                    <td style={styles.td}>
                      <img
                        src={`http://localhost:3010/${category?.image}`}
                        className="card-img-top"
                        alt={category.name}
                        style={{ height: "150px", width: "100px" }}
                      />
                      {console.log(`http://localhost:3010/${category?.image}`)}
                    </td>
                    <td style={styles.td}>{category.name}</td>
                    <td style={styles.td}>
                      <button style={styles.deleteButton}>Delete</button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}

        {/* Display the list of categories */}
      </div>
    </Layout>
  );
};

const styles = {
  container: {
    maxWidth: "600px",
    margin: "50px auto 0",
    padding: "20px 40px",
    border: "1px solid #ccc",
    borderRadius: "5px",
    backgroundColor: "#fff",
    boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
  },
  heading: {
    fontSize: "24px",
    textAlign: "center",
    marginBottom: "20px",
  },
  formGroup: {
    marginBottom: "20px",
  },
  label: {
    display: "block",
    fontWeight: "bold",
    marginBottom: "5px",
  },
  input: {
    width: "100%",
    padding: "10px",
    border: "1px solid #ccc",
    borderRadius: "3px",
    fontSize: "16px",
  },
  button: {
    display: "block",
    width: "100%",
    padding: "10px",
    backgroundColor: "#ff6600",
    color: "#fff",
    border: "none",
    borderRadius: "3px",
    cursor: "pointer",
    fontSize: "18px",
    marginTop: "10px",
  },

  table: {
    width: "100%",
    borderCollapse: "collapse",
    marginTop: "20px",
    border: "1px solid #ccc",
  },
  th: {
    backgroundColor: "#f2f2f2",
    padding: "10px",
    textAlign: "left",
    borderBottom: "1px solid #ddd",
    fontWeight: "bold",
  },
  td: {
    padding: "10px",
    textAlign: "left",
    borderBottom: "1px solid #ddd",
  },
  addButton: {
    display: "block",
    width: "200px",
    padding: "10px",
    backgroundColor: "#ff6600",
    color: "#fff",
    border: "none",
    borderRadius: "3px",
    cursor: "pointer",
    fontSize: "18px",
    marginTop: "10px",
    marginBottom: "20px",
    transition: "background-color 0.3s ease-in-out",
  },
  deleteButton: {
    display: "block",
    width: "110px",
    padding: "10px",
    backgroundColor: "#ff6600",
    color: "#fff",
    border: "none",
    borderRadius: "3px",
    cursor: "pointer",
    fontSize: "13px",
    transition: "background-color 0.3s ease-in-out",
  },
  addButtonHover: {
    backgroundColor: "#ff4500",
  },
};

export default Categories;
