import React, { useState } from "react";
import Layout from "./Layout";
import axios from "axios";
import { toast } from "react-toastify";
const Register = () => {
  // State variables for input fields, validation errors, and password strength
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [nameError, setNameError] = useState("");
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [confirmPasswordError, setConfirmPasswordError] = useState("");
  const [passwordStrength, setPasswordStrength] = useState("");

  // Function to handle name input change (optional)
  const handleNameChange = (e) => {
    const value = e.target.value;
    setName(value);

    // Validation logic for name (optional)
    if (value.trim() === "") {
      setNameError("Name is required");
    } else {
      setNameError("");
    }
  };

  // Function to handle email input change
  const handleEmailChange = (e) => {
    const value = e.target.value;
    setEmail(value);

    // Validation logic for email
    if (!value.includes("@")) {
      setEmailError("Invalid email format");
    } else {
      setEmailError("");
    }
  };

  // Function to calculate and set password strength
  const calculatePasswordStrength = (value) => {
    // You can implement your own password strength logic here
    // For simplicity, let's assume it's strong if it's at least 8 characters long
    if (value.length >= 8) {
      setPasswordStrength("Strong");
    } else if (value.length >= 6) {
      setPasswordStrength("Medium");
    } else {
      setPasswordStrength("Weak");
    }
  };

  // Function to handle password input change
  const handlePasswordChange = (e) => {
    const value = e.target.value;
    setPassword(value);

    // Validation logic for password
    if (value.length < 6) {
      setPasswordError("Password must be at least 6 characters long");
    } else {
      setPasswordError("");
    }

    // Calculate and set password strength
    calculatePasswordStrength(value);
  };

  // Function to handle confirmPassword input change
  const handleConfirmPasswordChange = (e) => {
    const value = e.target.value;
    setConfirmPassword(value);

    // Validation logic for confirmPassword
    if (value !== password) {
      setConfirmPasswordError("Passwords do not match");
    } else {
      setConfirmPasswordError("");
    }
  };

  // Function to handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post(
        "http://localhost:3010/auth/userSignUp",
        { name, email, password }
      );
      console.log("User created:", response.data);

      if (response.data.success) {
        toast.success(response.data.message);
        setName("");
        setEmail("");
        setPassword("");
        setConfirmPassword("");
        setConfirmPasswordError("");
        setEmailError("");
        setNameError("");
        setPasswordStrength("");
        setPasswordError("");
      } else {
        toast.error(response.data.error);
      }
    } catch (error) {
      console.error("Error creating user:", error);
    }
    // Validation logic here (same as before)

    // If validation passes, you can proceed with registration logic here
    // Send a request to your backend with the name, email, and password
  };

  // Define styles as an object
  const styles = {
    container: {
      maxWidth: "400px",
      margin: "50px auto 0",
      padding: "20px 40px", // Added padding from the right
      border: "1px solid #ccc",
      borderRadius: "5px",
      backgroundColor: "#fff",
      boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
    },
    heading: {
      fontSize: "24px",
      textAlign: "center",
      marginBottom: "20px",
    },
    formGroup: {
      marginBottom: "20px",
    },
    label: {
      display: "block",
      fontWeight: "bold",
      marginBottom: "5px",
    },
    input: {
      width: "100%",
      padding: "10px",
      border: "1px solid #ccc",
      borderRadius: "3px",
      fontSize: "16px",
    },
    button: {
      display: "block",
      width: "100%",
      padding: "10px",
      backgroundColor: "#ff6600",
      color: "#fff",
      border: "none",
      borderRadius: "3px",
      cursor: "pointer",
      fontSize: "18px",
    },
    buttonHover: {
      backgroundColor: "#0056b3",
    },
    passwordStrength: {
      marginTop: "5px",
      color: "green",
      fontWeight: "bold",
    },
    weakPassword: {
      color: "red",
    },
    mediumPassword: {
      color: "orange",
    },
    strongPassword: {
      color: "green",
    },
  };

  return (
    <Layout>
      <div style={styles.container}>
        <h2 style={styles.heading}>Register</h2>
        <form onSubmit={handleSubmit}>
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="name">
              Name:
            </label>
            <input
              style={styles.input}
              type="text"
              id="name"
              value={name}
              onChange={handleNameChange}
              required
            />
            <div style={{ color: "red" }}>{nameError}</div>
          </div>
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="email">
              Email:
            </label>
            <input
              style={styles.input}
              type="email"
              id="email"
              value={email}
              onChange={handleEmailChange}
              required
            />
            <div style={{ color: "red" }}>{emailError}</div>
          </div>
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="password">
              Password:
            </label>
            <input
              style={styles.input}
              type="password"
              id="password"
              value={password}
              onChange={handlePasswordChange}
              required
            />
            <div style={{ color: "red" }}>{passwordError}</div>
            <div
              style={
                passwordStrength === "Weak"
                  ? styles.weakPassword
                  : passwordStrength === "Medium"
                  ? styles.mediumPassword
                  : styles.strongPassword
              }
              className={styles.passwordStrength}
            >
              {password.length > 0
                ? `Password Strength: ${passwordStrength}`
                : ""}
            </div>
          </div>
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="confirmPassword">
              Confirm Password:
            </label>
            <input
              style={styles.input}
              type="password"
              id="confirmPassword"
              value={confirmPassword}
              onChange={handleConfirmPasswordChange}
              required
            />
            <div style={{ color: "red" }}>{confirmPasswordError}</div>
          </div>
          <button style={styles.button} type="submit">
            Register
          </button>
        </form>
      </div>
    </Layout>
  );
};

export default Register;
