// CartContext.js
import React, { createContext, useState, useContext, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { useLoading } from "./LoadingContext";

// Create the CartContext
const CartContext = createContext();

// Custom hook to use the CartContext
export const useCart = () => {
  return useContext(CartContext);
};

// CartProvider component
export const CartProvider = ({ children }) => {
  const [cart, setCart] = useState(() => {
    // Initialize cart from localStorage or an empty array if not found
    const storedCart = localStorage.getItem("cart");
    return storedCart ? JSON.parse(storedCart) : [];
  });
  const navigate = useNavigate();
  const { isLoading, showLoading, hideLoading, toggleLoading } = useLoading();
  const nav = (p) => {
    showLoading();
    setTimeout(() => {
      navigate(p);
      hideLoading();
    }, 900);
  };

  // Function to update cart and persist in localStorage
  const updateCart = (newCart) => {
    setCart(newCart);
    localStorage.setItem("cart", JSON.stringify(newCart));
  };

  // Add item to the cart
  const addItemToCart = (item) => {
    const existingItem = cart.find((cartItem) => cartItem._id === item._id);

    if (existingItem) {
      // If the item already exists, increment its quantity
      const updatedCart = cart.map((cartItem) =>
        cartItem._id === item._id
          ? { ...cartItem, quantity: cartItem.quantity + 1 }
          : cartItem
      );
      updateCart(updatedCart);
      nav("/cart");
      toast.success("Item already in cart - Quantity Updated");
    } else {
      // If it's a new item, add it to the cart with quantity 1
      const updatedCart = [...cart, { ...item, quantity: 1 }];
      updateCart(updatedCart);
      toast.success("Item Added");
      nav("/cart");
    }
  };

  // Remove item from the cart
  const removeItemFromCart = (itemId) => {
    const updatedCart = cart.filter((item) => item._id !== itemId);
    updateCart(updatedCart);
    toast.success(`Item removed`);
    nav("/cart");
  };

  const increaseItemQuantity = (itemId) => {
    const updatedCart = cart.map((item) =>
      item._id === itemId ? { ...item, quantity: item.quantity + 1 } : item
    );
    updateCart(updatedCart);
  };

  const decreaseItemQuantity = (itemId) => {
    const updatedCart = cart.map((item) =>
      item._id === itemId && item.quantity > 1
        ? { ...item, quantity: item.quantity - 1 }
        : item
    );
    updateCart(updatedCart);
  };

  const calculateTotalPrice = () => {
    return cart.reduce((total, item) => total + item.price * item.quantity, 0);
  };

  // Clear the entire cart
  const clearCart = () => {
    setCart([]);
    nav("");
    toast.success("Cart Cleared");
    // Clear localStorage as well
    localStorage.removeItem("cart");
  };

  // Use useEffect to watch for changes in the cart and update localStorage
  useEffect(() => {
    localStorage.setItem("cart", JSON.stringify(cart));
  }, [cart]);

  return (
    <CartContext.Provider
      value={{
        cart,
        addItemToCart,
        removeItemFromCart,
        clearCart,
        calculateTotalPrice,
        increaseItemQuantity,
        decreaseItemQuantity,
      }}
    >
      {children}
    </CartContext.Provider>
  );
};
