import { BrowserRouter, Routes, Route } from "react-router-dom";
import Loading from "./componenets/Loading";
import { useLoading } from "./componenets/LoadingContext";
import Navbar from "./componenets/Navbar";
import Footer from "./componenets/Footer";
import Home from "./componenets/Home";
import Layout from "./componenets/Layout";
import Login from "./componenets/Login";
import Register from "./componenets/Register";
import "react-toastify/dist/ReactToastify.css";
import DashBoard from "./componenets/admin/Dashboard";
import Dashboard from "./componenets/user/Dashboard";
import ProtectAdmin from "./componenets/admin/ProtectAdmin";
import Cart from "./componenets/Cart";
import Products from "./componenets/Products";
import Categories from "./componenets/admin/Categories";
import User from "./componenets/admin/User";
import Pro from "./componenets/admin/Pro";
import AdminNav from "./componenets/admin/AdminNav";
import { useAuth } from "./componenets/AuthContext";
import { CartProvider } from "./componenets/CartContext";
import Details from "./componenets/Details";
import Category from "./componenets/Category";
import Slider from "./componenets/admin/Slider";
export default function App() {
  const { isLoading } = useLoading();
  const { updateUser, user, clearUser } = useAuth();
  return (
    <BrowserRouter>
      <CartProvider>
        <Navbar />
        {user?.role == 1 ? <AdminNav /> : ""}
        {isLoading ? (
          <Loading />
        ) : (
          <>
            <Routes>
              {/* <Route path="" element={<Layout />} /> */}
              <Route path="" element={<Home />} />
              <Route path="/login" element={<Login />} />
              <Route path="/products" element={<Products />} />
              <Route path="/cart" element={<Cart />} />
              <Route path="/register" element={<Register />} />
              <Route path="/Details/:id" element={<Details />} />
              <Route path="/Category/:id" element={<Category />} />

              {/* admin routes   */}

              <Route path="/admin/DashBoard" element={<DashBoard />} />
              <Route path="/admin/Categories" element={<Categories />} />
              <Route path="users" element={<User />} />
              <Route path="/admin/products" element={<Pro />} />
              <Route path="/admin/user" element={<Register />} />
              <Route path="/admin/Slider" element={<Slider />} />

              {/* user routes  */}

              <Route path="/user/dashboard" element={<Dashboard />} />
            </Routes>
          </>
        )}
      </CartProvider>
      <Footer />
    </BrowserRouter>
  );
}
