import React, { useState } from "react";
import Layout from "./Layout";
import { toast } from "react-toastify";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useLoading } from "./LoadingContext";
import { useAuth } from "./AuthContext";
const Login = () => {
  // State variables for input fields
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const { isLoading, showLoading, hideLoading, toggleLoading } = useLoading();
  const { updateUser } = useAuth();
  const navigate = useNavigate();
  const nav = (p) => {
    showLoading();
    setTimeout(() => {
      navigate(p);
      hideLoading();
    }, 900);
  };
  // Function to handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!email || !password) {
      toast.error("All fields Are required");
    }
    try {
      const response = await axios.post(
        "http://localhost:3010/auth/userLogin",
        { email, password }
      );
      console.log("User Login:", response.data);

      if (response.data.success) {
        toast.success(response.data.message);
        if (response.data.role === 1) {
          const updatedUser = { role: 1, token: response.data.token };
          updateUser(updatedUser);
          nav("/admin/DashBoard");
        } else if (response.data.role === 0) {
          const updatedUser = { role: 0, token: response.data.token };
          updateUser(updatedUser);
          nav("/user/dashboard");
        }
      } else {
        toast.error(response.data.error);
        setEmail("");
        setPassword("");
      }
    } catch (error) {
      console.error("Error creating user:", error);
    }
    // Validation logic here (same as before)

    // If validation passes, you can proceed with registration logic here

    // Perform login logic here, e.g., send a request to your backend
    // with the email and password
  };

  // Define styles as an object
  const styles = {
    container: {
      maxWidth: "400px",
      margin: "50px auto 0",
      padding: "20px 40px", // Added padding from the right
      border: "1px solid #ccc",
      borderRadius: "5px",
      backgroundColor: "#fff",
      boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
    },
    heading: {
      fontSize: "24px",
      textAlign: "center",
      marginBottom: "20px",
    },
    formGroup: {
      marginBottom: "20px",
    },
    label: {
      display: "block",
      fontWeight: "bold",
      marginBottom: "5px",
    },
    input: {
      width: "100%",
      padding: "10px",
      border: "1px solid #ccc",
      borderRadius: "3px",
      fontSize: "16px",
    },
    button: {
      display: "block",
      width: "100%",
      padding: "10px",
      backgroundColor: "#ff6600",
      color: "#fff",
      border: "none",
      borderRadius: "3px",
      cursor: "pointer",
      fontSize: "18px",
    },
    buttonHover: {
      backgroundColor: "#0056b3",
    },
  };

  return (
    <Layout>
      <div style={styles.container}>
        <h2 style={styles.heading}>Login</h2>
        <form onSubmit={handleSubmit}>
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="email">
              Email:
            </label>
            <input
              style={styles.input}
              type="email"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </div>
          <div style={styles.formGroup}>
            <label style={styles.label} htmlFor="password">
              Password:
            </label>
            <input
              style={styles.input}
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
          </div>
          <button style={styles.button} type="submit">
            Login
          </button>
        </form>
      </div>
    </Layout>
  );
};

export default Login;
