import React from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { useLoading } from "./LoadingContext";
import { useAuth } from "./AuthContext";
import { toast } from "react-toastify";

const navbarStyles = {
  backgroundColor: "#333",
  color: "#fff",
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  padding: "10px 20px",
  // width: "100vw",
  overflow: "hidden",
};

const logoStyles = {
  fontSize: "24px",
  fontWeight: "bold",
};

const navStyles = {
  display: "flex",
  alignItems: "center",
};

const navLinksStyles = {
  listStyle: "none",
  display: "flex",
  gap: "20px",
};

const navLinkStyles = {
  fontSize: "16px",
  cursor: "pointer",
  transition: "color 0.3s",
  textDecoration: "none", // Remove underlines from links
  color: "#fff", // Set the default link color
};

const hoverStyles = {
  color: "#ff6600", // Change the color on hover to your desired color
};

const searchBoxStyles = {
  border: "1px solid #ccc",
  padding: "5px 10px",
  borderRadius: "4px",
  marginRight: "20px",
  marginLeft: "30px",
  width: "200px", // Adjust the width as needed
};

const searchButtonStyles = {
  backgroundColor: "#ff6600",
  color: "#fff",
  border: "none",
  padding: "5px 10px",
  borderRadius: "4px",
  cursor: "pointer",
};

const Navbar = () => {
  const { updateUser, user, clearUser } = useAuth();
  const { isLoading, showLoading, hideLoading, toggleLoading } = useLoading();
  const navigate = useNavigate();
  const nav = (p) => {
    showLoading();
    setTimeout(() => {
      navigate(p);
      hideLoading();
    }, 900);
  };
  const log = () => {
    clearUser();
    nav("");
  };
  return (
    <nav style={navbarStyles}>
      <div style={logoStyles}>E-Commerce</div>
      {user?.role == 1 ? (
        ""
      ) : (
        <div>
          <input type="text" placeholder="Search..." style={searchBoxStyles} />
          <button style={searchButtonStyles}>Search</button>
        </div>
      )}

      <div style={navStyles}>
        <ul style={navLinksStyles}>
          {user?.role === 1 ? (
            <li>
              <NavLink
                to="/"
                exact
                style={navLinkStyles}
                activeStyle={hoverStyles}
                onClick={log}
              >
                LogOut
              </NavLink>
            </li>
          ) : user?.role === 0 ? (
            <>
              {" "}
              <li>
                <NavLink
                  to=""
                  exact
                  style={navLinkStyles}
                  activeStyle={hoverStyles}
                  onClick={() => nav("")}
                >
                  Home
                </NavLink>
              </li>
              <li>
                <NavLink
                  to="/products"
                  style={navLinkStyles}
                  activeStyle={hoverStyles}
                  onClick={() => nav("/products")}
                >
                  Products
                </NavLink>
              </li>
              <li>
                <NavLink
                  to="/categories"
                  style={navLinkStyles}
                  activeStyle={hoverStyles}
                  onClick={() => nav("/categories")}
                >
                  Categories
                </NavLink>
              </li>
              <li>
                <NavLink
                  to="/cart"
                  style={navLinkStyles}
                  activeStyle={hoverStyles}
                  onClick={() => nav("/cart")}
                >
                  Cart
                </NavLink>
              </li>{" "}
              <li>
                <NavLink
                  to="/"
                  exact
                  style={navLinkStyles}
                  activeStyle={hoverStyles}
                  onClick={log}
                >
                  LogOut
                </NavLink>
              </li>
            </>
          ) : (
            <>
              {" "}
              <li>
                <NavLink
                  to="/"
                  exact
                  style={navLinkStyles}
                  activeStyle={hoverStyles}
                  onClick={() => nav("/")}
                >
                  Home
                </NavLink>
              </li>
              <li>
                <NavLink
                  to="/products"
                  style={navLinkStyles}
                  activeStyle={hoverStyles}
                  onClick={() => nav("/products")}
                >
                  Products
                </NavLink>
              </li>
              <li>
                <NavLink
                  to="/login"
                  style={navLinkStyles}
                  activeStyle={hoverStyles}
                  onClick={() => nav("/login")}
                >
                  Login
                </NavLink>
              </li>
              <li>
                <NavLink
                  to="/register"
                  style={navLinkStyles}
                  activeStyle={hoverStyles}
                  onClick={() => nav("/register")}
                >
                  Register
                </NavLink>
              </li>
            </>
          )}
        </ul>
      </div>
    </nav>
  );
};

export default Navbar;
